  }
  
  .banner-style-three .lower-box p a span{
    position: relative;
    padding-right: 20px;
  }
  
  .banner-style-three .lower-box p a span:before{
    position: absolute;
    content: "\f101";
    font-family: 'flaticon';
    bottom: -3px;
    right: 0px;
    font-size: 20px;
    font-weight: 40;
    transition: all 500ms ease;
