  .banner-style-three .content-box .inner-box h2 span{
    position: relative;
    font-size: 20px;
    top: 12px;
    right: 10px;
  }
  
  .banner-style-three .image-layer .image-1{
    position: absolute;
    left: 30px;
    bottom: 0px;
  }
  
  .banner-style-three .image-layer .image-2{
    position: absolute;
    top: 180px;
    right: 290px;
  }
  
  .banner-style-three .image-layer .image-3{
    position: absolute;
    right: 160px;
    top: 430px;
    z-index: 1;
  }
  
  .banner-style-three .image-layer .image-4{
    position: absolute;
    right: 340px;
    bottom: 220px;
    z-index: 1;
  }
  
  .float-bob-y {
    animation-name: float-bob-y;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    -webkit-animation-name: float-bob-y;
    -webkit-animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    -moz-animation-name: float-bob-y;
    -moz-animation-duration: 2s;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: linear;
    -ms-animation-name: float-bob-y;
    -ms-animation-duration: 2s;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: linear;
    -o-animation-name: float-bob-y;
    -o-animation-duration: 2s;
    -o-animation-iteration-count: infinite;
    -o-animation-timing-function: linear;
  }
  
  .float-bob-x{
    animation-name: float-bob-x;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    -webkit-animation-name: float-bob-x;
    -webkit-animation-duration: 15s;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    -moz-animation-name: float-bob-x;
    -moz-animation-duration: 15s;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: linear;
    -ms-animation-name: float-bob-x;
    -ms-animation-duration: 15s;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: linear;
    -o-animation-name: float-bob-x;
    -o-animation-duration: 15s;
    -o-animation-iteration-count: infinite;
    -o-animation-timing-function: linear;
  }
  
  
  /** feature-section **/
  
  .feature-section{
    position: relative;
    padding-bottom: 120px;
    z-index: 1;
  }
  
  .feature-block-one .inner-box{
    position: relative;
    display: block;
    overflow: hidden;
    border-radius: 5px;
    border: 1px solid #eae8e5;
    background: #fff;
    padding: 69px 30px 31px 29px;
    transition: all 500ms ease;
  }
  
  .feature-block-one .inner-box:hover{
    border-color: transparent;
  }
  
  .feature-block-one .inner-box:before{
    position: absolute;
    content: '';
    background: -webkit-linear-gradient(-90deg, #890f3d 0%, #ce193d 100%);
    width: 100%;
    height: 0%;
    left: 0px;
    top: 0px;
    border-radius: 5px;
    transition: all 500ms ease;
  }
  
  .feature-block-one .inner-box:hover:before{
    height: 100%;
  }
  
  .feature-block-one .inner-box .icon-inner{
    position: relative;
    display: inline-block;
    margin-bottom: 33px;
  }
  
  .feature-block-one .inner-box .icon-box{
    position: relative;
    display: inline-block;
    width: 70px;
    height: 70px;
    line-height: 70px;
    background: #fff;
    text-align: center;
    border-radius: 50%;
    box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.10);
  }
  
  .feature-block-one .inner-box .icon-inner .shape{
    position: absolute;
    width: 110px;
    height: 110px;
    top: -30px;
    right: -30px;
    background-repeat: no-repeat;
    transition: all 500ms ease;
  }
  
  .feature-block-one .inner-box:hover .icon-inner .shape{
    opacity: 0.2;
  }
  
  .feature-block-one .inner-box h6{
    display: block;
    font-size: 14px;
    line-height: 24px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 3px;
    transition: all 500ms ease;
  }
  
  .home_3 .feature-block-one .inner-box h6{
    color: var(--theme-color-three);
  }
  
  .feature-block-one .inner-box:hover h6{
    color: #fff;
  }
  
  .feature-block-one .inner-box h3{
    display: block;
    font-size: 22px;
    line-height: 32px;
    font-weight: 600;
    margin-bottom: 14px;
  }
  
  .feature-block-one .inner-box h3 a{
    display: inline-block;
    color: #1b1d24;
  }
  
  .feature-block-one .inner-box h3 a:hover{
    text-decoration: underline;
  }
  
  .feature-block-one .inner-box:hover h3 a,
  .feature-block-one .inner-box:hover p{
    color: #fff;
  }
  
  .feature-block-one .inner-box p{
    margin-bottom: 19px;
    transition: all 500ms ease;
  }
  
  .feature-block-one .inner-box:hover .link-box a{
    color: #fff;
  }
  
  
