  /** faq-page-section **/
  
  .faq-page-section{
    position: relative;
    padding: 120px 0px;
  }
  
  .faq-page-section .search-form{
    position: relative;
    margin-bottom: 60px;
  }
  
  .faq-page-section .search-form .form-group{
    position: relative;
    margin: 0px;
  }
  
  .faq-page-section .search-form .form-group input[type='search']{
    position: relative;
    display: block;
    width: 100%;
    height: 58px;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    font-size: 17px;
    color: #6e6e6e;
    padding: 10px 60px 10px 30px;
    transition: all 500ms ease;
  }
  
  .faq-page-section .search-form .form-group input:focus{
    border-color: var(--theme-color);
  }
  
  .faq-page-section .search-form .form-group button[type='submit']{
    position: absolute;
    top: 18px;
    right: 30px;
    font-size: 18px;
    color: #787878;
    cursor: pointer;
    transition: all 500ms ease;
  }
  
  .faq-page-section .search-form .form-group input:focus + button,
  .faq-page-section .search-form .form-group button:hover{
    color: var(--theme-color);
  }
  
  .faq-sidebar{
    position: relative;
    display: block;
    padding-right: 100px;
  }
  
  .faq-page-section .sidebar-side{
    position: relative;
  }
  
  .faq-page-section .sidebar-side:before{
    position: absolute;
    content: '';
    background: #e8e8e8;
    width: 1px;
    height: 100%;
    top: 0px;
    right: 85px;
  }
  
  .faq-sidebar .category-list li{
    position: relative;
    display: block;
    margin-bottom: 22px;
  }
  
  .faq-sidebar .category-list li:last-child{
    margin-bottom: 0px;
  }
  
  .faq-sidebar .category-list li a{
    position: relative;
    display: inline-block;
    font-size: 18px;
    line-height: 28px;
    color: #6e6e6e;
    font-weight: 700;
    padding-right: 26px;
  }
  
  .faq-sidebar .category-list li a:hover{
    color: #1b1e31;
  }
  
  .faq-sidebar .category-list li a:before{
    position: absolute;
    content: "\f103";
    font-family: 'flaticon';
    font-size: 22px;
    top: 0px;
    right: 0px;
    color: #1b1e31;
    font-weight: 400;
    opacity: 0;
    transition: all 500ms ease;
  }
  
  .faq-sidebar .category-list li a:hover:before{
    opacity: 1;
  }
  
  
  
  /** error-section **/
  
  .error-section{
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 180px 0px;
  }
  
  .error-section .content-box{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  
  .error-section .scroll-text{
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    width: 100%;
    height: 100%;
  }
  
  .error-section .scroll-text .text-box-one,
  .error-section .scroll-text .text-box-two{
    position: absolute;
    left: 0px;
    width: 100%;
    min-width: 7000px;
    padding: 20px 0px;
    background: var(--theme-color);
  }
  
  .error-section .scroll-text .text-inner{
    -moz-animation: scroll-left 90s linear infinite;
    -webkit-animation: scroll-left 90s linear infinite;
    animation: scroll-left 90s linear infinite;
  }
  
  .error-section .scroll-text .text-box-one{
    top: 0px;
  }
  
  .error-section .scroll-text .text-box-two{
    bottom: 0px;
  }
  
  .error-section .scroll-text h4{
    position: relative;
    display: inline-block;
    font-size: 20px;
    line-height: 30px;
    color: #fff;
    font-weight: 600;
    text-transform: capitalize;
    margin: 0px 32px;
  }
  
  @-moz-keyframes scroll-left {
    0% { -moz-transform: translateX(0%); }
    100% { -moz-transform: translateX(-100%); }
  }
  
  @-webkit-keyframes scroll-left {
    0% { -webkit-transform: translateX(0%); }
    100% { -webkit-transform: translateX(-100%); }
  }
  
  .error-section .content-box h3{
    position: relative;
    display: block;
    text-align: left;
    font-size: 36px;
    line-height: 36px;
    font-weight: 600;
    color: var(--theme-color);
  }
  
  .error-section .content-box h1{
    display: block;
    font-size: 300px;
    line-height: 180px;
    font-weight: 600;
    margin-bottom: 92px;
  }
  
  .error-section .content-box h1:before{
    position: absolute;
    content: '';
    background-image: url(../../public/assets/images/icons/icon-89.png);
    width: 220px;
    height: 220px;
    left: 50%;
    transform: translateX(-50%);
    top: 23px;
    background-repeat: no-repeat;
  }
  
  .error-section .content-box h2{
    display: block;
    font-size: 36px;
    line-height: 46px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .error-section .content-box p{
    display: block;
    margin-bottom: 32px;
  }
  
  .error-section .pattern-layer{
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 1543px;
    height: 725px;
    background-repeat: no-repeat;
  }
  

  @media screen and (max-width: 767px) {
    .menu-area .mobile-nav-toggler { 
      display: block;

    }
  }