@media screen and (max-width: 767px) {
  .mobile-menu .nav-logo {
    padding-bottom: 0px;
  }
  .menu-area {
    width: 100% !important;
  }

  .outer-container {
    .outer-box {
      padding: 10px 20px;
    }
  }

  .main-header {
    height: 5.6rem !important;
  }
  .menu-area .mobile-nav-toggler {
    padding-left: 60px;
    padding-top:0px;
  }

  .main-header .logo-box {
    & figure {
      .logo-img {
        width: 75% !important;
      }
    }
  }

  .logo-box {
    .logo-title span{
        /* margin-left:2.8rem; */
        font-size: 12px;
    }
  }
  .menu-area .mobile-nav-toggler .icon-bar  {
    height:5px;
    margin-bottom: 6px;
    width:40px
  }
}


@media screen and (max-width: 400px) {
    .menu-area .mobile-nav-toggler {
        padding:0px;
    }
}

@media screen and (max-width: 500px) {
    .main-header {
        .menu-area { 
           position: fixed !important;
           justify-content: space-evenly; 
           background-color: #fff !important;
           top:0px;
       }
       .outer-box {
        padding: 0px !important;
       }    
       .logo-box {
        figure {
            top:-10px;
            .logo-img {
                width:75% !important
            }
        }
       }    
    }
}

